<template>
  <div id="app">
    <child></child>
    <childb></childb>
    <childb title="这是父组件传给子组件的值"></childb>
    <childb :title="title"></childb>
    <sona @sendFather="sendfaather"></sona>
    <div>子组件传过来的值：{{message}}</div>
    <div class="router">
      <div><h2>路由相关知识点</h2></div>
      <button @click="toProject">跳转到路由新界面</button>
      <div class="routera">
        <router-link to="/home">home页面</router-link>
        <router-link to="/about">about页面</router-link>
        <router-link :to="'/user/'+userId">user页面</router-link>
        <div>通过js代码跳转路由</div>
        <button @click="toHome">首 页</button>
        <button @click="toAbout">关 于</button>
        <button @click="toUser">用户界面</button>
        <router-view></router-view>
      </div>
    </div>
    <div class="routernest"> 
      <button @click="nesta">路由嵌套</button>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>

// 在vue中，父子组件的关系可以总结为prop向下传递，事件向上传递。

import child from './components/child.vue'
import childb from './components/childb.vue'
import sona from './components/son1.vue'

export default {
  name: 'App',
  components: {
    child,
    childb,
    sona
  },
  data() {
    return {
      title: '这是父组件传给子组件的第二个值',
      message:'',
      userId: 'admin'
    }
  },
  methods: {
    sendfaather(val){
      this.message = val
    },
    toHome() {
      this.$router.push('/home');
      
    },
    toAbout() {
      this.$router.push('/about');
    },
    toUser() {
      this.$router.push('/user/'+this.userId);
    },
    toProject() {
      const { href } = this.$router.resolve({path:"/project"});
      window.open(href,'_blank');
    },
    nesta() {
      this.$router.push('/nesta');
    }
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.router {
  background: greenyellow;
  width: 100%;
  height: 200px;
}
.routernest {
  background-color: khaki;
  width: 100%;
  height: 400px;
}
</style>
