<template>
    <div>
        <!-- 在子组件上定义一个事件 -->
        <button @click="send">点击子组件向父组件传值</button>
    </div>
</template>

<script>

// 子组件通过事件的方式向父组件传值
export default {
    name: 'sona',
    data() {
        return {
            sonMessage: '这是来自子组件的消息'
        }
    },
    methods: {
        send() {
            // 使用$emit(eventName,optionalPayload)触发事件
            // eventname为父组件的@名称
            this.$emit('sendFather', this.sonMessage);
        }
    },
}
</script>