import Vue from 'vue'
import VueRouter from 'vue-router';
import vueRouter from 'vue-router'

// 导入路由组件
import Home from '../views/component/home.vue'
import About from '../views/component/about.vue'
import User from '../views/component/user.vue'

// 安装路由插件
Vue.use(vueRouter);

// es6的写法，配置路由映射关系的数组
const routers = [
    {
        path:'/',
        redirect:'/home'
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/about',
        component: About
    },
    {
        path: '/user/:userId',
        component: User
    },
    {
        path: '/project',
        // 使用路由的懒加载
        component: () => import('../views/project/index.vue')
    },
    {
        path: '/nesta',
        component: () => import('../views/nest/nesta.vue'),
        children:[
            {
                // 嵌套的子路由不加/
                path: 'nestb',
                component: () => import('../views/nest/nestb.vue'),
                children:[
                    {
                        path: 'nestc',
                        component: () => import('../views/nest/nestc.vue')
                    }

                ]
            },
            
        ]
    }
]

const router = new VueRouter({
    // 这里注意一下单词的书写，routes属性对路由配置对象
    routes:routers,
    mode:'history'
})

// 导出路由对象
export default router